*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

 body {
  background: url('./images/background.webp');
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 30vh; 
  width: 100%;
  /* display: flex; */
  align-items: center;
  justify-content: center;
  margin: 0px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
/*
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
*/
}


.header{
  /*background-color: #f4511e; */
  background: url('./images/banner.webp');
  background-repeat: no-repeat;
  background-size: cover; 
  color: white; 
  width: 100%; 
  /* background-color: #f4511e; */
  text-align: left; 
  height: 170px;
  padding-left: 180px;
  padding-top: 50px; 
}

.header-title{
  font-size: 40px;
}

.header-text{
  font-size: 20px;
  font-style: italic;
}

#subtitle {
 display: inline;
 position: relative;
 color: white; 
 top: 15px;
 padding-left: 15px;
 font-weight: bold;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 18px;
  border-radius: 4px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent */
/* the header to increase when the font gets bigger */
.header a.logo {
  font-size: 22px;
  font-weight: bold;

}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ced6cb;
  color: green;
  font-weight: bold;
}

/* Style the active/current link*/
.header a.active {
  background-color: green;
  color: white;
}

/* Float the link section to the right */
.header-right a {
  background-color: #dcdcdc;
  float: right;
  font-style: italic;
  padding: 10px;
  color: black;
  margin: 10px;
  opacity: 0.55;
}


#comment,
#from {
  font-size: 14px;
  font-style: italic;
  opacity: 0.6;
}

.section{ 
  width: 120%;
  text-align: center;
  align-items: center; 
  justify-content: center;
  
}

.section > div {
  padding: 10px;
}

.small-div {
  width: 20%;
}

.big-div {
  width: 80%;
}

.bg-grey{
  /* background-color:#f6f6f6; */
  opacity: 0.8;
}

.orange {
  color: #f4511e;
}

.div-title {
  font-size: 30px;
  font-weight: 600;
}

.contact-container2,
.contact-container  {
  flex-flow: column;
  padding-top: 70px;
  display: flex;
  justify-content: space-between;
  width:96%; 
}

.contact-container2
 {
  line-height: 1.5rem;
  padding-top: 50px;
  padding-bottom: 40px;  
}

.div-title{
  text-align: center;
}


.contact-form{
  display: flex;
  flex-direction: row;
  /*flex-flow: row wrap;*/
  justify-content: space-between;
  width:80%;
}

.column-footer {
  width: 100%;
  justify-content: center;
  text-align: center;
}

#sect1{
  width: 100%;
  justify-content: center;
  text-align: right;
}

#sect2{
  width: 100%;
  justify-content: center;
}

.contact-form > div {
  width: 500px;
  max-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 30px;
}

.input-field {
  height: 40px;
  margin: 15px 0;
}

.contact-btn {
  margin-top: 15px;
  height: 40px;
  color: white;
  border: 0;
  background-color: #f4511e;
}

@media only screen and (min-width:320px) and (max-width:725px){
  .contact-form{
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
    width:80%;
  }

  .section{
    
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  }


  .small-div {
    width: 80%;
  }
}


